@import url(https://fonts.googleapis.com/css?family=Cormorant+Unicase);
body {
  background-color: bisque;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(247, 241, 241);
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(126, 118, 118);
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #f37474;
}

/* Utilities */

/* Mobile Styles */
@media (max-width: 700px) {
  .hide-sm {
    display: none;
  }

  .grid-2,
  .grid-3,
  .grid-4 {
    grid-template-columns: 1fr;
  }

  /* Text Styles */
  .x-large {
    font-size: 3rem;
  }

  .large {
    font-size: 2rem;
  }

  .lead {
    font-size: 1rem;
  }
}

dl {
  font-family: 'Gugi', cursive;
}

div {
  margin-top: 50px;
  font-family: 'Cormorant Unicase', serif;
}

.text-center {
  text-align: center;
}

.search-block {
  margin: 0 auto;
  width: 250px;
  height: 50px;
  background: rgb(77, 221, 249);
  background: #45aaf2;
  overflow: hidden;
  border-radius: 100px;
  position: relative;
  box-shadow: 0px 10px 15px -5px rgba(0, 0, 0, 0.4);
}

.search-block .icon {
  position: absolute;
  pointer-events: none;
  left: 14px;
  top: 50%;
  transform: translateY(-50%);
  transition-duration: 0.8s;
  z-index: 0;
}

.search-block:hover .icon {
  transform: translateY(-50%) scale(1.3) rotate(3deg);
}

.search-block .icon:before {
  margin-top: -4px;
  display: block;
  content: '';
  height: 20px;
  width: 20px;
  border-radius: 20px;
  background: white;
}

.search-block .icon:after {
  position: absolute;
  bottom: -8px;
  right: 5px;
  display: block;
  content: '';
  height: 15px;
  width: 4px;
  border-radius: 20px;
  transform: rotate(-45deg);
  transform-origin: center top;
  background: white;
}

.search-block input[type='search'] {
  height: 100%;
  width: 100%;
  border: 0px;
  background: transparent;
  position: relative;
  outline: 0;
  -webkit-appearance: none;
  padding: 0px 20px 0px 50px;
  font-size: 18px;
  color: #2f3542;
  z-index: 2;
}

.search-block input[type='search']:focus + .icon {
  transform: translate(50%, -50%) scale(24) rotate(180deg);
}

.search-block input[type='search']::-webkit-input-placeholder {
  color: white;
  opacity: 1;
}

.search-block input[type='search']:-ms-input-placeholder {
  color: white;
  opacity: 1;
}

.search-block input[type='search']::-ms-input-placeholder {
  color: white;
  opacity: 1;
}

.search-block input[type='search']::placeholder {
  color: white;
  opacity: 1;
}

.search-block input[type='search']:-ms-input-placeholder {
  color: white;
}

.search-block input[type='search']::-ms-input-placeholder {
  color: white;
}

